async getbyType($event){
  console.log($event.target.value);
  console.log(this.booking_type);
  console.log(this.booking_status);
  if($event.target.value == 'all'){
    this.apiCall.api_getPlots();
  }
  if($event.target.value == 'for sale'){
    this.booking_status.status = $event.target.value;
    this.apiCall.getPlotsbystatus(this.booking_status)
    this.global.BookingbyStatus.subscribe(res =>{
      console.log(res);
      this.allLqPlots = res;
      console.log(this.allLqPlots);
    });
  }
  if($event.target.value == 'booked'){
    this.booking_status.status = $event.target.value;
    this.apiCall.getPlotsbystatus(this.booking_status);
    this.global.BookingbyStatus.subscribe(res =>{
      console.log(res);
      this.allLqPlots = res;
      console.log(this.allLqPlots);
    });
  }
  if($event.target.value == 'Commercial'){
    this.booking_type.type = $event.target.value;
    this.apiCall.getPlotsbyType(this.booking_type)
    this.global.BookingbyType.subscribe(res =>{
      console.log(res);
      this.allLqPlots = res;
      console.log(this.allLqPlots);
    });
  }
  if($event.target.value == 'Residential'){
    this.booking_type.type = $event.target.value;
    this.apiCall.getPlotsbyType(this.booking_type)
    this.global.BookingbyType.subscribe(res =>{
      console.log(res);
      this.allLqPlots = res;
      console.log(this.allLqPlots);
    });
  }
  else{
    console.log('no data');
  }
}

